import React from "react";
import clsx from "clsx";
import { Link } from "react-router-dom";

import { Drawer, List, ListItem, ListItemText, Box, Grid, Avatar, Tooltip} from "@material-ui/core"; 

import { green } from '@material-ui/core/colors';
import { red } from '@material-ui/core/colors';
import { blue } from '@material-ui/core/colors';
import { yellow } from '@material-ui/core/colors';

import CompareIcon from '@material-ui/icons/Compare';
import DashboardIcon from '@material-ui/icons/Dashboard';
import TimelineIcon from '@material-ui/icons/Timeline';

import ListItemIcon from '@material-ui/core/ListItemIcon';
import PeopleIcon from '@material-ui/icons/People';
import BarChartIcon from '@material-ui/icons/BarChart';
import AssignmentIcon from '@material-ui/icons/Assignment';
import HomeIcon from '@material-ui/icons/Home';
import VpnKeyOutlinedIcon from '@material-ui/icons/VpnKeyOutlined';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import {useStyles} from './useStyles';


const MyDrawer = ({ title, variant, open, onClose, onItemClick })=>{
	  const classes = useStyles()
	  return (
      <Drawer
        variant={variant}
        open={open}
        onClose={onClose}
        classes={{
          paper: classes.drawerPaper
        }}
      >
	  		<Box mt={2} />
		<Grid
			container
			spacing={1}
			justify="center"
			alignItems="center"
		>
		  <Avatar alt='COVID' className={classes.avatar} >
			  <VpnKeyOutlinedIcon color="primary" />
		  </Avatar>
		  <Avatar alt='COVID' className={classes.avatar} >
			  {title}
		  </Avatar>
		  <Avatar className={classes.orange}>{title.slice(0, 1)}</Avatar>
		</Grid> 
		<Box mt={2} />
        <div
          className={clsx({
            [classes.toolbarMargin]: variant === "persistent"
          })}
        />
        <List>
		  <Tooltip arrow title=' COVID-19   2  '>
          <ListItem
            button
            component={Link}
            to="/"
            onClick={onItemClick("Must know COVID-19")}
          >

		  <ListItemIcon>
			<HomeIcon style={{ color: red[500] }}/>
		  </ListItemIcon>
            <ListItemText></ListItemText>
		  
          </ListItem>
		  </Tooltip>
		  <Tooltip arrow title='    8'>
		  <ListItem
            button
            component={Link}
            to="/dashboard"
            onClick={onItemClick("COVID-19 Updated")}
          >
			  <ListItemIcon>
				<DashboardIcon color="secondary"/>
			  </ListItemIcon>
            <ListItemText></ListItemText>
          </ListItem>
		  </Tooltip>
		  <Tooltip arrow title='     '>
		  <ListItem
            button
            component={Link}
            to="/lineplot"
            onClick={onItemClick("Trend and Information")}
          >
			  <ListItemIcon>
				<TimelineIcon color="secondary"/>
			  </ListItemIcon>

            <ListItemText> </ListItemText>
          </ListItem>
		  </Tooltip>
		  <Tooltip arrow title='       '>
		  <ListItem
            button
            component={Link}
            to="/comparecountry"
            onClick={onItemClick("Compare with others")}
          >
			  <ListItemIcon>
				<CompareIcon color="primary"/>
			  </ListItemIcon>
            <ListItemText></ListItemText>
          </ListItem>
		  </Tooltip>


		 </List>

	</Drawer>
  )
};

export default MyDrawer
